<template>
  <div class="page">
    <div class="page-title">A* (A STAR)</div>
    <div class="page-description">使用canvas繪製</div>
    <div class="float-panel">
      <div class="control-group">
        <label class="label">寬</label>
        <input class="textbox" type="number" maxlength="200" v-model="rowCount" />
      </div>
      <div class="control-group">
        <label class="label">高</label>
        <input class="textbox" type="number" max="200" v-model="colCount" />
      </div>
      <div class="control-group">
        <button class="btn" :class="{active:mode == 'draw'}" @click="switchMode('draw')">繪製邊界</button>
        <button class="btn" :class="{active:mode == 'erase'}" @click="switchMode('erase')">清除邊界</button>
        <button class="btn" :class="{active:mode == 'place'}" @click="switchMode('place')">放置</button>
        <button class="btn" :class="{active:mode == 'goto'}" @click="switchMode('goto')">移動到</button>
      </div>
    </div>
    <grid-canvas class="grid-canvas" v-model="gridMeta" :mode="mode"></grid-canvas>
  </div>
</template>

<script>

import CompGridCanvas from "./GridCanvas";
const MAXSIZE = 200;
export default {
  components: { "grid-canvas": CompGridCanvas },
  data() {
    return {
      rowCount: 20,
      colCount: 20,
      mode: "draw"
    };
  },
  watch: {
    rowCount(v) {
      if (v > MAXSIZE) {
        this.rowCount = MAXSIZE;
      }
    },
    colCount(v) {
      if (v > MAXSIZE) {
        this.colCount = MAXSIZE;
      }
    }
  },
  computed: {
    gridMeta() {
      return {
          w:this.rowCount,
          h:this.colCount,
          x:0,
          y:0,
          scale:1
      };
    }
  },
  methods: {
    switchMode(mode) {
      this.mode = mode;
    }
  }
};
</script>

<style lang="scss" scope>
.page {
  display: flex;
  height: calc(100vh - 3rem);
  flex-direction: column;
}

.page-description {
  color: #555;
  font-size: 0.9rem;
}
.float-panel {
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  padding: 1rem;
  color: #fff;
  border-radius: 1rem;
}

.grid-canvas {
  border: 1px solid;

  width: 100%;
  flex-grow: 1;
}
</style>