<template>
  <canvas
    ref="gridCanvas"
    @mousedown="mousedown"
    @mouseup="mouseup"
    @mousemove="mousemove"
    @mousewheel="mousewheel"
  ></canvas>
</template>

<script>
import Grid from "./Grid";
import Astar from "./Astar";
let animationTimer = null;
export default {
  props: {
    value: {
      type: Object
    },
    searchEngine: {
      type: Object
    },
    mode: {
      type: String
    }
  },
  data() {
    let grid = new Grid(this.value.w, this.value.h);
    let astar = new Astar(grid);
    return {
      mouse: {
        x: 0,
        y: 0,
        button: -1
      },
      graphMeta: {
        x: 0,
        y: 0,
        scale: 1
      },
      nodeMeta: {
        w: 20,
        h: 20
      },
      currentNode: null,
      astar: astar,
      grid:grid,
      canvas: null
    };
  },
  computed: {
    graphSize() {
      return {
        w: this.grid.w * this.nodeMeta.w,
        h: this.grid.h * this.nodeMeta.h
      };
    },
  },
  methods: {
    mousedown(e) {
      this.mouse.button = e.button;
      this.mouse.x = e.offsetX;
      this.mouse.y = e.offsetY;
    },
    mouseup(e) {
      e.preventDefault();
      if (this.mouse.button == 0) {
        if (this.mode == "place") {
          let target = this.shotNode(e.offsetX, e.offsetY);
          if (target.node) {
            this.currentNode = target.node;
          }
          this.draw();
        } else if (this.mode == "goto") {
          let target = this.shotNode(e.offsetX, e.offsetY);
          if (target.node) {
            let result = this.searchEngine.search(
              this.currentNode,
              target.node
            );
            if (result.length > 0) {
              this.currentNode = target;
              let intervalId = setInterval(() => {
                this.currentNode = result.shift();
                this.draw();
                if (result.length <= 0) {
                  clearInterval(intervalId);
                }
              }, 10);
            }
          }

          this.draw();
        }
      }
      this.mouse.button = -1;
    },
    mousemove(e) {
      let mouse = this.mouse;
      if (this.mouse.button >= 0) {
        if (this.mouse.button == 2) {
          //pan
          let graphMeta = this.graphMeta;
          graphMeta.x += (e.offsetX - mouse.x) * 1;
          graphMeta.y += (e.offsetY - mouse.y) * 1;
        } else if (this.mouse.button == 0) {
          if (this.mode == "draw") {
            let target = this.shotNode(e.offsetX, e.offsetY);
            if (target.node) {
              target.node.weight = 0;
            }
          } else if (this.mode == "erase") {
            let target = this.shotNode(e.offsetX, e.offsetY);
            if (target.node) {
              target.node.weight = 1;
            }
          }
        }
        mouse.x = e.offsetX;
        mouse.y = e.offsetY;

        this.draw();
      }
    },
    mousewheel(e) {
      if (e.deltaY > 0) {
        //zoom out
        this.graphMeta.scale -= 0.5;
      } else if (e.deltaY < 0) {
        this.graphMeta.scale += 0.5;
      }
      if (this.graphMeta.scale < 0.5) {
        this.graphMeta.scale = 0.5;
      } else if (this.graphMeta.scale > 10) {
        this.graphMeta.scale = 10;
      }
      this.draw();
    },
    draw() {
      cancelAnimationFrame(animationTimer);
      let canvas = this.canvas;
      let ctx = canvas.getContext("2d");
      let graphMeta = this.graphMeta;
      let grid = this.grid;
      let nodeMeta = this.nodeMeta;
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      ctx.save();
      ctx.translate(graphMeta.x, graphMeta.y);
      ctx.scale(graphMeta.scale, graphMeta.scale);
      let graphSize = this.graphSize;
      ctx.strokeRect(0, 0, graphSize.w, graphSize.h);
      for (var i = 0; i < grid.w; ++i) {
        for (var j = 0; j < grid.h; ++j) {
          let node = grid.data[j][i];
          if (this.currentNode == node) {
            ctx.fillStyle = "#F00";
            ctx.fillRect(
              i * nodeMeta.w,
              j * nodeMeta.h,
              nodeMeta.w,
              nodeMeta.h
            );
          } else if (node.weight == 0) {
            ctx.fillStyle = "#222";
            ctx.fillRect(
              i * nodeMeta.w,
              j * nodeMeta.h,
              nodeMeta.w,
              nodeMeta.h
            );
          }
          ctx.strokeRect(
            i * nodeMeta.w,
            j * nodeMeta.h,
            nodeMeta.w,
            nodeMeta.h
          );
        }
      }
      ctx.restore();
      animationTimer = requestAnimationFrame(this.draw);
    },
    shotNode(x, y) {
      let realX = Math.floor(
        (x - this.graphMeta.x) / this.graphMeta.scale / this.nodeMeta.w
      );
      let realY = Math.floor(
        (y - this.graphMeta.y) / this.graphMeta.scale / this.nodeMeta.h
      );
      let target = this.grid.data[realY];
      if (target) {
        target = target[realX];
      }
      return { x: realX, y: realY, node: target };
    },
    refreshCanvasSize() {
      let w = this.canvas.clientWidth;
      let h = this.canvas.clientHeight;
      this.canvas.width = w;
      this.canvas.height = h;
      this.draw();
    }
  },
  watch:{
      value(v){
          this.grid.resize(v.w,v.h)
      }
  },
  mounted() {
    this.canvas = this.$refs.gridCanvas;
    this.canvas.addEventListener("contextmenu", e => e.preventDefault());
    this.refreshCanvasSize();
    this.draw();
    window.addEventListener("resize", () => this.refreshCanvasSize());
  }
};

// function handleClick(){

// }
</script>

