import Node from './Node';
function Grid(w, h) {
    this.data = [];
    this.resize(w, h);
}

Grid.prototype.resize = function (w, h) {
    const grid = this.data;
    if(grid.length > h)
    {
        grid.length = h;
    }
    for (let y = 0; y < h; y++) {
        if (grid.length < y + 1) {
            grid.push([]);
        }
        let row = grid[y];
        if(row.length > w)
        {
            row.length = w;
        }
        for (let x = 0; x < w; x++) {
            if (row.length < x + 1) {
                row.push(new Node(x, y, 1));
            }
        }
    }
    this.w = w;
    this.h = h;
}

Grid.prototype.neighborOf = function (node) {
    let x = node.x;
    let y = node.y;
    var neighbors = [];
    if (this.data[y] && this.data[y][x - 1]) {
        neighbors.push(this.data[y][x - 1])
    }
    if (this.data[y] && this.data[y][x + 1]) {
        neighbors.push(this.data[y][x + 1])
    }
    if (this.data[y - 1] && this.data[y - 1][x]) {
        neighbors.push(this.data[y - 1][x])
    }
    if (this.data[y + 1] && this.data[y + 1][x]) {
        neighbors.push(this.data[y + 1][x])
    }
    return neighbors;
}

Grid.prototype.getNodes = function(){
    var result = [];
    this.data.forEach(m=>result = result.concat(m));
    return result;
}

Grid.prototype.getDistance = function(a, b){
    let dx = Math.pow(a.x - b.x,2);
    let dy = Math.pow(a.y - b.y,2);
    return Math.sqrt(dx + dy);
}


export default Grid;