<template>
  <div class="page">
    <div class="page-title">A* (A STAR)</div>
    <div class="float-panel">
      <div class="control-group">
        <label class="label">寬</label>
        <input class="textbox" type="number" v-model="rowCount" />
      </div>
      <div class="control-group">
        <label class="label">高</label>
        <input class="textbox" type="number" v-model="colCount" />
      </div>
      <div class="control-group">
        <button class="btn" :class="{active:mode == 'draw'}" @click="switchMode('draw')">繪製邊界</button>
        <button class="btn" :class="{active:mode == 'erase'}" @click="switchMode('erase')">清除邊界</button>
        <button class="btn" :class="{active:mode == 'place'}" @click="switchMode('place')">放置/指定目標</button>
      </div>
    </div>
    <div class="grid" @mousedown="gridMouseDown" @mouseup="gridMouseUp">
      <div class="grid-row" v-for="(row, r) in gridRows" :key="r">
        <div
          class="grid-col"
          v-for="(col,c) in row"
          :key="c"
          @click="clickBox(col)"
          @mouseenter="gridMouseEnter(col)"
          :style="nodeStyle(col)"
        >
          <div v-if="onStep(col)">A</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Grid from "./Grid";
import Astar from "./Astar";
const MAXSIZE = 200;
export default {
  data() {
    let grid = new Grid(20, 20);
    let astar = new Astar(grid);
    return {
      rowCount: grid.w,
      colCount: grid.h,
      grid: grid,
      astar: astar,
      mode: "draw",
      isMouseDown: false,
      previous: null,
      step: null
    };
  },
  watch: {
    rowCount(v) {
      if (v > MAXSIZE) {
        this.rowCount = MAXSIZE;
      }
      else{
        this.grid.resize(v, this.colCount);
      }
    },
    colCount(v) {
      if (v > MAXSIZE) {
        this.colCount = MAXSIZE;
      }
      else{
        this.grid.resize(this.rowCount, v);
      }
    }
  },
  computed: {
    gridRows() {
      return this.grid.data;
    }
  },
  methods: {
    clickBox(box) {
      if (this.mode == "draw") {
        box.weight = 0;
      } else if (this.mode == "erase") {
        box.weight = 1;
      } else if (this.mode == "place") {
        let result = this.astar.search(this.previous, box);
        if (result.length > 0) {
          this.previous = box;
          let intervalId = setInterval(() => {
            this.step = result.shift();
            if (result.length <= 0) {
              clearInterval(intervalId);
            }
          }, 10);
        }
      }
    },
    gridMouseUp() {
      this.isMouseDown = false;
    },
    gridMouseDown() {
      this.isMouseDown = true;
    },
    gridMouseEnter(box) {
      if (this.isMouseDown) {
        if (this.mode == "draw") {
          box.weight = 0;
        } else if (this.mode == "erase") {
          box.weight = 1;
        }
      }
    },
    switchMode(mode) {
      this.mode = mode;
    },
    onStep(node) {
      return node == this.step;
    },
    nodeStyle(node) {
      let style = {
        background: node.weight == 0 ? "#000" : "#FFF"
      };
      return style;
    }
  }
};
</script>

<style lang="scss">
.float-panel {
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  padding: 1rem;
  color: #fff;
  border-radius: 1rem;
}
.grid {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: nowrap;
}
.grid-row {
  display: flex;
  justify-content: center;
}
.grid-col {
  border: 1px solid #aaa;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
}
</style>