import MinBinaryHeap from './MinBinaryHeap';
function Astar(graph) {
    this.markedNodes = [];
    this.graph = graph;
}

Astar.prototype.search = function(start,goal){
    //清理上次標註的節點
    this.clean();
    if(!start)
    {
        return [goal];
    }
    var heap = new MinBinaryHeap(e=>e.f);
    start.h = this.graph.getDistance(start,goal);
    this.mark(start);
    heap.push(start);

    while(heap.length > 0){
        let current = heap.pop();
        if(current == goal)
        {
            return current.getPath();
        }

        //已被估算節點標記為封閉，該節點本次搜尋不會再進入
        current.closed = true;

        let neighbors = this.graph.neighborOf(current);
        for (let i = 0; i < neighbors.length; i++) {
            const neighbor = neighbors[i];
            if(neighbor.isBlocked()){
                continue;
            }
            
            let g = current.g + neighbor.getScore(current);
            let isVisited = neighbor.visited;

            //未被造訪過 或者 相鄰節點的分數比目前節點的分數高
            if(!isVisited || g < neighbor.g)
            {
                neighbor.visited = true;
                neighbor.parent = current;
                neighbor.h = neighbor.h || this.graph.getDistance(neighbor,goal);
                neighbor.g = g;
                //標記下次搜尋要清乾淨的節點
                this.mark(neighbor);
                if(!isVisited)
                {
                    heap.push(neighbor);
                }
                else{
                    heap.bubbleTarget(neighbor);
                }
            }

        }
    }

    return [];
}

Astar.prototype.mark = function (node) {
    this.markedNodes.push(node);
}

Astar.prototype.clean = function () {
    this.markedNodes.forEach(n=>n.clean());
    this.markedNodes.splice(0,this.markedNodes.length);
}

export default Astar;