/**
 * Create a heap
 * @param {function} valuator element value provider
 */
function MinBinaryHeap(valuator){
    this.nodes = [];
    this.valuator = valuator || (e=>e);
    Object.defineProperty(this,'length',{
        get(){
            return this.nodes.length;
        }
    })
}

/**
 * Push new element to heap
 * @param {*} item 
 */
MinBinaryHeap.prototype.push = function(item){
    this.nodes.push(item);
    this.bubble(this.nodes.length - 1);
}

/**
 * Pop the mininum element from heap
 */
MinBinaryHeap.prototype.pop = function(){
    let item = this.nodes[0];
    let tail = this.nodes.pop();
    if(this.nodes.length > 0)
    {
        this.nodes[0] = tail;
        this.sink(0);
    }
    return item;
}

/**
 * Bubble from target to root
 * @param {number} n  target index
 */
MinBinaryHeap.prototype.bubble = function(n){
    let node = this.nodes[n];
    while(n > 0)
    {
        //root is 0
        let indexOfParent = ((n + 1) >> 1) - 1;
        let parent = this.nodes[indexOfParent];
        if(this.valuator(node) < this.valuator(parent))
        {
            //switch
            this.nodes[indexOfParent] = node;
            this.nodes[n] = parent;
            n = indexOfParent;
        }
        else{
            break;
        }
        
    }
}

/**
 * Do MinHeapify from target index
 * @param {*} n target index
 */
MinBinaryHeap.prototype.sink = function(n = 0){

    let node = this.nodes[n];
    let length = this.nodes.length;
    let score = this.valuator(node);

    for(;;)
    {
        let right = (n+1) << 1; // 2(n+1)
        let left = right - 1;

        let swap = null;
        let leftScore;
        if(left < length)
        {
            leftScore = this.valuator(this.nodes[left]);
            if(leftScore < score){
                swap = left;
            }
        }

        if(right < length)
        {
            let rightScore = this.valuator(this.nodes[right]);
            if(rightScore < (swap === null ? score : leftScore))
            {
                swap = right;
            }
        }

        if(swap !== null)
        {
            this.nodes[n] = this.nodes[swap];
            this.nodes[swap] = node;
            n = swap;
        }
        else{
            break;
        }
    }
}

/**
 * Bubble from target
 * @param {*} node target node
 */
MinBinaryHeap.prototype.bubbleTarget = function(node){
    this.bubble(this.nodes.indexOf(node));
}

export default MinBinaryHeap;