function Node(x,y,weight){
    this.x = x;
    this.y = y;
    //權重，如果為0，則不可通行
    this.weight = weight;
    //起點到該點距離
    this.g = 0;
    //該點到目標距離
    this.h = 0;
    //g 與 f 的和
    Object.defineProperty(this,'f',{
        get(){
            return this.g + this.h;
        }
    });
    
    //已被造訪標記
    this.visited = false;
    //封閉不可進入標記
    this.closed = false;
    //到該點的父節點參考
    this.parent = null;

}

Node.prototype.getScore = function(){
    return this.weight;
}

Node.prototype.isBlocked = function(){
    return this.weight === 0 || this.closed;
}

Node.prototype.clean = function(){
    this.g = 0;
    this.h = 0;
    this.visited = false;
    this.closed = false;
    this.parent = null;
}

Node.prototype.getPath = function(){
    let current = this;
    let path = [];
    while(current.parent)
    {
        path.unshift(current);
        current = current.parent;
    }
    return path;
}

export default Node;